.card {
    border-radius: 10px;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.5);     
}

@media (min-width: 768px) {
.container {
    position: relative;
    /* width: 50%; */
  }
  .image {
    display: block;
    width: 100%;
    height: auto;
  }
  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(240, 222, 200, .75);
    overflow: hidden;
    width: 0;
    height: 100%;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
  }
  .container:hover .overlay {
    width: 100%;
  }
  .text {
    color: rgba(84, 95, 111, 1);
    font-size: 30px;
    position: absolute;
    font-weight: bolder;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    white-space: nowrap;
  }
}
@media (max-width: 767px) {
    .card-title {
        margin-top: 1rem;
    }
}


